___CSS_LOADER_EXPORT___.push([module.id, `/* Header e Navegação */
header {
    background-color: #252525;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
}

a {
    text-decoration: none;
    color: inherit;
}

ul {
    list-style: none;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 1rem;
    max-width: 1200px;
    margin: 0 auto;
}

.logo h1 {
    font-size: 1.8rem;
    color: var(--primary-color);
    margin: 0;
}

.menu-toggle {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--primary-color);
}

.nav-links {
    display: flex;
}

.nav-links li {
    margin-left: 30px;
}

.nav-links a {
    font-weight: 500;
    color: #c7c7c7;
    transition: color 0.3s ease;
    position: relative;
}

.nav-links a:hover,
.nav-links a.active {
    color: var(--primary-color);
}

.nav-links a.active::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -5px;
    left: 0;
    background-color: var(--primary-color);
}`, "",{"version":3,"sources":["webpack://./frontend/assets/css/nav.css"],"names":[],"mappings":"AAAA,uBAAuB;AACvB;IACI,yBAAyB;IACzB,wCAAwC;IACxC,gBAAgB;IAChB,MAAM;IACN,aAAa;AACjB;;AAEA;IACI,qBAAqB;IACrB,cAAc;AAClB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,kBAAkB;IAClB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,iBAAiB;IACjB,2BAA2B;IAC3B,SAAS;AACb;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,eAAe;IACf,2BAA2B;AAC/B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,cAAc;IACd,2BAA2B;IAC3B,kBAAkB;AACtB;;AAEA;;IAEI,2BAA2B;AAC/B;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,WAAW;IACX,WAAW;IACX,YAAY;IACZ,OAAO;IACP,sCAAsC;AAC1C","sourcesContent":["/* Header e Navegação */\r\nheader {\r\n    background-color: #252525;\r\n    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: 1000;\r\n}\r\n\r\na {\r\n    text-decoration: none;\r\n    color: inherit;\r\n}\r\n\r\nul {\r\n    list-style: none;\r\n}\r\n\r\n.navbar {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 15px 1rem;\r\n    max-width: 1200px;\r\n    margin: 0 auto;\r\n}\r\n\r\n.logo h1 {\r\n    font-size: 1.8rem;\r\n    color: var(--primary-color);\r\n    margin: 0;\r\n}\r\n\r\n.menu-toggle {\r\n    display: none;\r\n    font-size: 1.5rem;\r\n    cursor: pointer;\r\n    color: var(--primary-color);\r\n}\r\n\r\n.nav-links {\r\n    display: flex;\r\n}\r\n\r\n.nav-links li {\r\n    margin-left: 30px;\r\n}\r\n\r\n.nav-links a {\r\n    font-weight: 500;\r\n    color: #c7c7c7;\r\n    transition: color 0.3s ease;\r\n    position: relative;\r\n}\r\n\r\n.nav-links a:hover,\r\n.nav-links a.active {\r\n    color: var(--primary-color);\r\n}\r\n\r\n.nav-links a.active::after {\r\n    content: '';\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 2px;\r\n    bottom: -5px;\r\n    left: 0;\r\n    background-color: var(--primary-color);\r\n}"],"sourceRoot":""}]);
